import { React, useRef, Fragment } from 'react'
import { Dialog, Transition } from '@headlessui/react'
import iconSet from "../../selection.json";
import IcomoonReact, { iconList } from "icomoon-react";
export default function Dialogs({ isOpen, setIsOpen }) {
  let completeButtonRef = useRef()
  return (
    <>
      <Transition appear show={isOpen} as={Fragment}>
        <Dialog
          as="div"
          className="fixed inset-0 z-10 overflow-y-auto"
          onClose={() => setIsOpen(false)}
        >
          <div className="min-h-screen px-4 text-center w-full">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0" />
            </Transition.Child>

            {/* This element is to trick the browser into centering the modal contents. */}
            <span
              className="inline-block h-screen align-middle"
              aria-hidden="true"
            >
              &#8203;
            </span>
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <div className="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded-2xl">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-gray-900"
                >
                  Come and contact me!
                </Dialog.Title>
                <div className="mt-2">
                  <ul>
                    <li className='text-lg py-3 flex items-center'><span ><IcomoonReact iconSet={iconSet} color="#444" size={30} icon="instagram" className='mr-3' /></span> Instagram: jupeng_0215</li>
                    <li className='text-lg py-3 flex items-center'><span ><IcomoonReact iconSet={iconSet} color="#444" size={30} icon="facebook2" className='mr-3' /></span>Facebook: Ju Peng Ng</li>
                    <li className='text-lg py-3 flex items-center'><span ><IcomoonReact iconSet={iconSet} color="#444" size={30} icon="gmail" className='mr-3' /></span>Gmail: jupeng2015@gmail.com</li>
                    <li className='text-lg py-3 flex items-center'><span ><IcomoonReact iconSet={iconSet} color="#444" size={30} icon="github" className='mr-3' /></span>Github: bowbowzai</li>
                  </ul>
                </div>

                <div className="mt-4">
                  <button
                    type="button"
                    className="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                    onClick={() => setIsOpen(false)}
                  >
                    Got it, thanks!
                  </button>
                </div>
              </div>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition>
    </>
  )
}

